.header {
    &__logo {
        margin: 0;
        text-decoration: none;
        display: flex;
        column-gap: 8px;
        align-items: center;

        &-title {
            text-wrap: nowrap;
            font-family: 'Captureit';
            cursor: pointer;
            color: white;
            letter-spacing: 4px;
            margin: 0;
            font-size: calc(24px + 2vmin);
            background-clip: text;
            text-shadow: 2px 2px 20px rgba(255,255,255,0.24);
        }

        &-image {
            width: 45px;
            animation: glowing 3s alternate infinite;
            background-color: rgba(255, 255, 255, 0.05);
            border-radius: 50%;
            padding: 4px;
        }
    }
}

@keyframes glowing {
    0% {
        filter: drop-shadow(0 0 12px rgba(255, 255, 255, 1));
    }

    20% {
        filter: drop-shadow(0 0 8px rgba(38, 94, 38, 0.7));
    }

    40% {
        filter: drop-shadow(0 0 10px rgba(48, 118, 48, 1));
    }

    60% {
        filter: drop-shadow(0 0 12px rgba(38, 110, 58, 0.7));
    }

    80% {
        filter: drop-shadow(0 0 14px rgba(28, 95, 78, 0.5));
    }

    100% {
        filter: drop-shadow(0 0 16px rgba(18, 75, 58, 1));
    }
}