.goods__filter{display:grid;grid-template-columns:auto 1fr 3fr;gap:8px;align-items:center;margin:0 0 12px}.goods__filter-search{position:relative}.goods__filter-search::before{content:"🔍";position:absolute;font-size:20px;display:grid;place-items:center;top:0;bottom:0;right:0;aspect-ratio:1/1}.goods__filter-input{padding:12px 4px;width:100%}.goods__filter-input::before{content:"🔍";position:absolute;top:0;left:0;width:100px;height:100px}.goods__filter-title{margin:0;white-space:nowrap;font-size:24px}.goods__filter-select{padding:12px 4px}.goods__list{list-style:none;margin:0;padding:0;display:grid;grid-template-columns:repeat(auto-fill, minmax(280px, 1fr));grid-gap:8px}.goods__item{display:flex;flex-direction:column;padding:8px;border-radius:4px;background-color:rgba(0,0,0,.2)}.goods__item-image{aspect-ratio:1/1}.goods__item-price{margin:0 0 20px}.goods__item-actions{margin:auto 0 0;display:grid;gap:8px;grid-template-columns:repeat(2, 1fr)}.goods__item-button{padding:8px;border:none;color:#fff;cursor:pointer;font-weight:bold;transition:.3s transform}.goods__item-button:hover{opacity:.8;transform:scale(1.05)}.goods__item-button:first-child{background-color:#001f3f}.goods__item-button:last-child{background-color:green}@media(max-width: 600px){.goods__wrapper{padding:0 8px 20px}.goods__filter{grid-template-columns:repeat(2, 1fr)}.goods__filter-search{grid-column:1/3}.goods__filter-title{font-size:calc(10px + 2.5vmin)}}