.store {
    padding: 0 20px 20px;
    background-color: lightblue;

    &__wrapper {
        display: flex;
        flex-direction: column;
        max-width: 1200px;
        margin: 0 auto;
    }

    &__title {
        text-align: center;
        font-size: calc(20px + 4vmin);
    }
}